.catalog {
  .catalog__pic {
    position: relative;
    margin-bottom: 1.5rem;

    picture {
      display: block;
      overflow: hidden;
    }
    img {
      width: 100%;
      transform: scale(1);
      transition: transform 0.4s ease-in-out;
    }

    a:hover {
      img {
        transform: scale(1.05);
      }
    }
  }
  .catalog__id {
    margin-bottom: 1rem;
    color: $color-gray;
    font-size: 1.4rem;
  }
  .catalog__title {
    margin-bottom: 1rem;
    line-height: 1.3;

    a {
      border-bottom: 1px solid transparent;
      text-decoration: none;
      color: $color-font;
      transition: border-color 0.2s ease-in-out;

      &:hover {
        border-bottom-color: $color-font;
        text-decoration: none;
      }
    }
  }
  .catalog__price {
    font-weight: 600;
    font-size: 2rem;
  }
}
// mod
.catalog--list {
  margin-bottom: 2rem;

  .catalog__items {
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    display: flex;
    flex-flow: row wrap;
  }
  .catalog__item {
    margin-bottom: 4rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    width: calc(100% / 2);

    @media (min-width: $breakpoint-tablet) {
      width: calc(100% / 3);
    }
    @media (min-width: $breakpoint-desktop) {
      width: calc(100% / 4);
    }
    @media (min-width: $breakpoint-desktop-extra) {
      width: calc(100% / 5);
    }
  }
}
// mod
.catalog--goods {
  .catalog__items {
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    display: flex;
    flex-flow: row wrap;
  }
  .catalog__item {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    width: 100%;

    @media (min-width: $breakpoint-desktop) {
      width: calc(100% / 2);
    }
  }
  .catalog__inner {
    padding: 2rem;
    box-shadow: 0px 2px 15px rgba(38, 50, 56, 0.1);
    border-radius: 3px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
  }
  .catalog__pic {
    width: 100%;

    @media (min-width: $breakpoint-tablet) {
      margin-bottom: 0;
      width: 12rem;
    }
  }
  .catalog__content {
    width: 100%;

    @media (min-width: $breakpoint-tablet) {
      padding-left: 1.5rem;
      width: calc(100% - 12rem);
    }
  }
}
// mod
.catalog--carousel {
  .catalog__carousel {
    max-width: 80%;

    @media (min-width: $breakpoint-tablet) {
      max-width: 100%;
    }
  }
  .catalog__overlay {
    padding: 2rem;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    display: flex;
    flex-flow: column wrap;
    pointer-events: none;
    opacity: 0;
    background-color: $color-white-80a;
    font-size: 1.6rem;
    transition: opacity 0.2s ease-in-out;

    .catalog__btn {
      margin-top: auto;
    }

    ul {
      list-style: none;

      li {
        color: $color-lightgray3;

        span, ins {
          color: $color-font;
        }
      }
    }
  }
  .catalog__item:hover .catalog__overlay {
    opacity: 1;
    pointer-events: all;
  }
}
